<!--正在直播-->
<template>
  <div class="living">
    <!-- 正在直播 -->
    <div class="live-ing">
      <div class="title">正在直播</div>
      <div class="live-wrap">
        <liveBox @click.native="handleToLiving(item)" :info="item" v-for="item in livingLists" :key="item.id"></liveBox>
      </div>
      <el-pagination
        v-if="pagination.total/pagination.pageSize>1"
        style="text-align:center"
        @current-change="handleCurrentChange"
        :current-page="pagination.pageNo"
        :page-size="pagination.pageSize"
        layout="total, prev, pager, next, jumper"
        :total="pagination.total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  components:{
    liveBox:()=>import("@/components/live-box")
  },
  data(){
    return {
      livingLists:[],
      pagination:{
        total:0,
        pageSize: 8,
        pageNo:1
      }
    }
  },
  created(){
    this.geLivingata()
  },
  methods:{
    geLivingata(){
      const params = {
        pageNo:this.pagination.pageNo,
        pageSize:this.pagination.pageSize
      }
      this.$api.geLivingata(params).then(res => {
        this.pagination.total = Number(res.totalDatas);
        this.livingLists = res.data;
      })
    },
    handleCurrentChange(currentPage){
      this.pagination.pageNo = currentPage;
      this.geLivingata()
    },
    handleToLiving(item){
      this.$router.push(`/liveCenter/video/play/${item.id}/${item.code}`)
    }
  }
}
</script>

<style lang="scss" scoped>
.living {padding: 32px;min-height: 100%;}
.live-ing >.title {color: #606266;line-height: 1;padding-bottom: 20px}
.live-ing .live-wrap {margin-left: -15px;margin-right: -15px;display: flex;flex-wrap: wrap;}
.live-ing .live-wrap > div {width: 25%;margin-bottom: 20px;}
</style>
<style>
.living .el-pagination .btn-prev {
  background: #F1F3F8;
}
.living .el-pagination .btn-next {
  background: #F1F3F8;
}
.living .el-pagination .el-pager li {
  background: #F1F3F8;
}
</style>